<template lang="html">
  <div class="account-modify-page">
    <!-- 头部 -->
    <default-head header="修改登录密码"></default-head>
    <white-title :text="modifyInfo.mobile" data-vv-name="用户手机号" v-validate="'required'" data-vv-value-path="text" class="borderB"></white-title>
    <form class="form-box">

      <div class="form-group-item">
        <div class="form-group-item-ctn">
          <input type="text" class="form-group-item-input" placeholder="请输入验证码" data-vv-name="验证码" v-validate="'required'" name="code" v-model="modifyInfo.smsCode">
          <input type="button" @click="getCode" ref="btnCode" value="发送验证码" class="get-code get-code-active">
        </div>
      </div>

      <form-line-light label="" inpuType="password"  v-model="modifyInfo.password" data-vv-name="新密码" v-validate="'required'" name="password"
      placeholder="新密码" class="borderB"></form-line-light>
      <form-line-light label="" inpuType="password" v-model="modifyInfo.passwordCopy" data-vv-name="确认新密码" v-validate="'required'" name="passwordCopy"
      placeholder="确认新密码" ></form-line-light>
      <sub-button text="提交" marginTop="1rem" @click.native="submitModify"></sub-button>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import formLine from '@/components/common/formLine'
import formLineLight from '@/components/common/formLineLight'
import whiteTitle from '@/components/common/whiteTitle'
import subButton from '@/components/common/subButton'

export default {
  data () {
    return {
      modifyInfo: {
        mobile: utils.getUser().mobile || '',
        password: '',
        passwordCopy: '',
        smsCode: '',
        type: 3
      },
      wait: 60
    }
  },
  computed: {
    userType () {
      return utils.getType()
    }
  },
  components: { defaultHead, formLine, formLineLight, subButton, whiteTitle },
  methods: {
    submitModify () {
      this.$validator.validateAll().then(() => {
        // 判断密码与确认棉是否一致】
        if (this.modifyInfo.password !== this.modifyInfo.passwordCopy) {
          minutils.Toast({ message: '密码与确认密码不一致', position: 'top' })
          return
        }
        this.ajaxUpdatePwd(this.modifyInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    getCode () {
      if (!this.modifyInfo.mobile) {
        minutils.Toast({ message: '手机号码不能为空', position: 'top' })
        return
      }
      this.ajaxSmsCode({mobile: this.modifyInfo.mobile, type: '3'})
    },
    time (e) {
      let btn = this.$refs.btnCode
      if (this.wait === 0) {
        btn.removeAttribute('disabled')
        btn.value = '获取验证码'
        this.wait = 60
      } else {
        btn.setAttribute('disabled', true)
        btn.value = this.wait + 's后重新发送'
        this.wait--
        setTimeout(() => this.time(btn), 1000)
      }
    },
    ajaxEnterpriseChangePwd (data) {
      api.enterpriseChangePwd(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          minutils.MessageBox({message: '您已成功修改登录密码'})
        }
      })
    },
    ajaxUpdatePwd (data) {
      let self = this
      api.updatePwd(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          minutils.MessageBox({
            message: '修改用户密码成功',
            callback () {
              self.$router.push('/accountmanage')
            }
          })
        }
      })
    },
    ajaxSmsCode (data) {
      api.smsCode(data).then(response => {
        let result = response.body
        if (result.isSuccess) this.time()
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.account-modify-page {
  padding-top: .88rem;
  .form-box {
    .form-group-item {
      display: flex;
      height: .88rem;
      line-height: .88rem;
      font-size: .32rem;
      padding: 0 .22rem;
      background: #fff;
      border-bottom: 1px solid @lineColor;
      .form-group-item-text {
        width: 1.34rem;
        color: @defaultColor;
      }
      .form-group-item-ctn {
        flex: 1;
        position: relative;
        height: .8rem;
        .form-group-item-input {
          width: 100%;
          border: none;
          font-size: .32rem;
          color: @lightColor;
          &::placeholder {
            color: @lightColor;
          }

        }
        .get-code, .error {
          position: absolute;
          right: 0;
          top: 0;
          color: @activeColor;
        }
        .get-code {
          top: .1rem;
          height: .68rem;
          line-height: .68rem;
          font-size: .3rem;
          border: 1px solid @activeColor;
          border-radius: .1rem;
          background: @whiteColor;
          width: 2.4rem;
          text-align: center;
        }
        .get-code:disabled {
          border-color: @lightColor;
          color: @defaultColor;
        }
      }
    }
  }
}

</style>



